import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)


const store = new Vuex.Store({
    state:{
        //
        store:'',
        curren:''
    },
    mutations:{
        //set 
        setValue(state,value){
            state.store = value
        },
        setValue2(state,value){
            state.curren = value
        }
    },
    getters:{
        //get 
        getValue: state => {
            return state.store
        },
        getValue2: state=>{
            return state.curren
        }
    }
})


export default store


